@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/OpenSans-Light.woff2") format("woff2"), url("../fonts/OpenSans-Light.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/OpenSans-Semibold.woff2") format("woff2"), url("../fonts/OpenSans-Semibold.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff"); }

html {
  font-size: 10px; }

body {
  margin: 0;
  font-family: 'Open Sans', 'Arial', sans-serif;
  font-weight: 400; }

img {
  max-width: 100%; }

a {
  color: inherit;
  text-decoration: none; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

.reset-text {
  margin: 0;
  padding: 0; }

.container {
  width: 88rem;
  margin: 0 auto; }

.header {
  position: absolute;
  left: 0;
  right: 0;
  padding: 2rem 0; }
  .header__logo {
    display: none; }
  .header__list {
    display: flex;
    padding: 0 2rem;
    padding-bottom: .9rem;
    margin: 0;
    border-bottom: 0.1rem solid rgba(255, 255, 255, 0.2);
    flex-wrap: wrap;
    font-size: 1.6rem;
    line-height: 3.2rem;
    font-weight: 300;
    color: rgba(255, 255, 255, 0.7);
    list-style: none; }
  .header__item {
    margin-right: 13.5rem; }
    .header__item:last-child {
      margin-right: 0; }
    .header__item:hover {
      color: white; }

.intro {
  display: flex;
  width: 100%;
  min-height: 82.5rem; }
  .intro__bg {
    position: absolute;
    width: 100%;
    height: 82.5rem;
    margin: 0 auto;
    z-index: -1; }
    .intro__bg img {
      width: 100%;
      height: 100%;
      object-fit: cover; }
  .intro .container {
    display: flex;
    justify-content: space-between; }

.about {
  width: 38rem;
  margin-top: 32rem;
  color: #FFFFFF; }
  .about__logo {
    margin-bottom: 3rem; }
  .about__title {
    margin-bottom: 2.4rem;
    font-weight: 700;
    font-size: 4.6rem;
    line-height: 6.4rem; }
  .about__description {
    font-weight: 300;
    font-size: 2rem;
    line-height: 3.2rem;
    color: rgba(255, 255, 255, 0.8); }

.callback {
  width: 33rem;
  margin-top: 32rem; }
  .callback__title {
    margin-bottom: 2rem;
    font-weight: 600;
    font-size: 17px;
    line-height: 28px;
    color: #ffffff;
    text-align: center; }
  .callback form {
    display: flex;
    flex-direction: column; }
  .callback input {
    padding: 1.2rem 2rem;
    margin-bottom: 2rem;
    border: .1rem solid #C9CCD4;
    border-radius: .6rem;
    font-weight: 400;
    font-size: 1.5rem;
    line-height: 2.4rem; }
    .callback input::placeholder {
      color: rgba(11, 11, 11, 0.5); }
  .callback button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 5rem;
    padding: 0;
    background-color: #0AD9C6;
    border: none;
    border-radius: .6rem;
    font-weight: 600;
    font-size: 1.5rem;
    line-height: 2.1rem;
    outline: none;
    cursor: pointer; }
